export default {
  name: "vip_deposit_config",
  data() {
    return {
      id: 0,
      keys: "",
      type: "QQ",
      name: "",
      wx: "",
      desc: "",
      list: []
    };
  },
  async created() {
    //获取数据
    let { data } = await this.$http.HallFunConfig.GetRechargeConfig({
      key: "recharge_config.lua"
    });
    // console.log(data);
    this.id = data.data[0].id;
    this.keys = data.data[0].sys_key;
    let res = JSON.parse(data.data[0].sys_val);
    // console.log(res);
    this.list = res;
  },
  methods: {
    //添加
    add() {
      this.list.push({});
    },

    // 保存
    async saveData() {
      // console.log('拿到数据,saveData');
      console.log(this.list);
      this.list.forEach(item => {
        if (item.undefined === "QQ") {
          item.type = "qq";
        } else if (item.undefined === "微信") {
          item.type = "wechat";
        }
      });
      // console.log(this.list);

      let { data } = await this.$http.HallFunConfig.PutRechargeConfig({
        keys: this.keys,
        id: this.id,
        values: JSON.stringify(this.list)
      });
      // console.log(data);
      if (data.code === 1 && data.msg === "ok") {
        this.$message({
          type: "success",
          message: "保存成功!"
        });
      } else {
        this.$message({
          type: "warning",
          message: "保存失败"
        });
      }
    },

    //删除
    del(index) {
      this.$confirm("确定删除吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.list.splice(index, 1);
          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    }
  }
};



